import { Util } from '@/config/util';
/* author rr */

/**
 * table
 * @param {Object} data 
 * @param {Function} callback 
 */
export const getTableList = function (data, callback) {
    Util.DataEncSubmit('menu_get.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });

};

